<template>
	<div>
		<el-row>
			<headers :title='title'></headers>
		</el-row>
		<el-row>
			<el-col :span='18' :offset='3'>
				<div class="post_container">
					<div class="post_c_t">
						<span>岗位详情</span>
						<span class='r' @click='r'>返回列表</span>
					</div>
					<div class="from_data">
						<el-row>
							<el-col :span='12' :offset='3'>
								<el-form ref="form" :model="form" label-width="100px">
  									<el-form-item label="岗位类型：">
  										{{form.post_type}}
 									</el-form-item>
 									<el-form-item label="岗位名称：">
  										{{form.name}}
 									</el-form-item>
 									<el-form-item label="岗位人数：" prop='num'>
  										{{form.num}}
 									</el-form-item>
 									<el-form-item label="岗位职责：">
 										<div v-for='(i,index) in form.responsibility.split("；")' :key='index'>
 											{{i}}
 										</div>
 										
 									</el-form-item>
 									<el-form-item label="任职要求：">
  										<div v-for='(i,index) in form.Requirement.split("；")' :key='index'>
 											{{i}}
 										</div>
 									</el-form-item>
 								</el-form>
							</el-col>
						</el-row>
					</div>
				</div>
			</el-col>
		</el-row>
	</div>
</template>

<script>
	import headers from '../../components/header.vue'
	export default{
		components:{
			headers
		},
		data(){
			return{
				title:'岗位详情',
				form:{
					post_type:'',
					name:'',
					num:'',
					responsibility:'',
					Requirement:'',
					id:''
				},
				options:[]
			}
		},
		methods:{
			r(){
				this.$router.push('/home/post_manage')
			}
		},
		mounted(){
			var arr2=window.sessionStorage.getItem('post_d')
			var arr=JSON.parse(arr2)
			console.log(arr)
			this.form.name=arr.jobName,
			this.form.post_type=arr.typeName,
			this.form.responsibility=arr.jobStatement,
			this.form.Requirement=arr.jobRequirement,
			this.form.num=arr.talentNumber
		}
	}
</script>

<style scoped>
	.post_container{
		width: 100%;
		min-height:500px;
		background: #fff;
		border: 1px solid #e9e9e9;
		box-sizing: border-box;
		background: #fff;
		/* padding: 0 20px; */
		margin: 20px 0;
	}
	.post_c_t{
		width: 100%;
		height: 50px;
		line-height: 50px;
		font-size: 14px;
		color: #666;
		border-bottom: 1px solid #e9e9e9;
		box-sizing: border-box;
		font-weight: bold;
	}
	.post_c_t>span{
		margin: 0 0 0 20px;
	}
	.from_data{
		width: 100%;
		margin: 50px 0;
	}
	.post_c_t>.r{
		float: right;
		margin: 0 20px 0 0;
		cursor: pointer;
	}
</style>
<style>
	.el-textarea .el-textarea__inner{
		resize: none;
	}
</style>